html{
	padding: 0;
	margin: 0;
}

body{
	padding: 0;
	margin: 0;
	position: relative;
	background-color: #25252F;
	color: #f8f8ff;
}
em, small, h5, h2 small{
	color: #909096;
}

a{
	color: #d93344;
}

.text-red{
  color: #d93344;
}
.text-green{
  color: #41CD64;
}

a:hover,
a:active,
a:focus{
	color: #d93344;
}

a:active,
a:focus,
button:active,
button:focus{
	outline: 0 none;
}

// input[type="checkbox"]:checked{
//   outline: 1px solid #41CD64;
// }
// input[type="checkbox"]:not(:checked){
//   outline: 1px solid #d93344;
// }

input[type="checkbox"],
label[for]{
  cursor: pointer;
}

.input-group-btn .btn.btn-primary{
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.bg-primary {
  background: #d93344 !important;
}

.all_time_title{
  margin-top: 70px;
  border-bottom: 2px solid #41CD64;
  padding: 8px 25px 12px;
}

.btn.btn-primary{
  background: #d93344;
  border-color: #d93344;
  box-shadow: 0 0 transparent;
}

.App .google-visualization-tooltip{
  padding: 7px 15px;
  background: lighten(#25252F, 5%);
  border: 0 none;
  box-shadow: 0 0 5px rgba(0,0,0,0.2)
}


.btn.btn-primary:not(:disabled):not(.disabled):active,
.btn.btn-primary:not(:disabled):not(.disabled):hover{
  background: saturate(#d93344, 15%);
  border-color: saturate(#d93344, 15%);
  outline: 0 none;
  box-shadow: 0 0 transparent;
}

input.form-control{
	border-radius: 2px;
}

h1 img{
	vertical-align: bottom;
}

h1.page_title{
  position: relative;
  top: 3px;
  font-size: 1.8rem;
  margin: 0;
}

h3{
	margin-top: 30px;
}
.invisible{
	visibility: hidden;
}
.totally_visible{
	visibility: visible;
}
.error{
	color: red;
}

.info_list{
	margin-top: 40px;
}

.info_list li{
	margin-bottom: 10px;
}

.img-20{
	width: 20px;
	height: 20px;
	display: inline-block;
}

.Header{
  padding-top: 15px;
	margin-bottom: 40px;
}

.OGS_logo{
	height: 30px;
}

.loading_wrapper{
	padding: 10px 15px;
	z-index: 10;
	text-align: center;
}

.content-wrapper{
	padding-top: 20px;
}

.stats_content{
	padding-bottom: 60px;
}
.stats_block{
	margin: 0;
	padding: 50px 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.1);;
}

.stats_block:first-child{
  padding-top: 0;
}

.stats_block:last-child{
	border-bottom: 0 none;
}
.pie-legend{
	list-style: none;
	padding: 0;
	margin: 10px 0;
	text-align: center;
}
.pie-legend li{
	display: inline-block;
	padding: 0 10px;
}
.pie-legend span {
	width: 10px;
	height: 10px;
	display: inline-block;
	vertical-align: baseline;
	margin-right: 3px;
}

.col-md-6 .pie-legend{
	font-size: 16px;
}

.col-md-6 .pie-legend span{
	width: 12px;
	height: 12px;
}
.gratz{
	margin-top: 20px;
}

.opponent_chart{
	margin-top: 30px;
}

.bar_chart{
	height: 3px;
	overflow: visible;
	position: relative;

	background: #d93344;
	background: linear-gradient(to right, #d93344 0%, #efc225 50%,#41CD64 100%);
}

.bar_chart a{
	position: absolute;
	display: block;
	top: -14px;
	margin-left: -16px;
	height: 32px;
	width: 32px;
	overflow: hidden;
}

.bar_chart a:focus,
.bar_chart a:active{
	outline: 0 none;
}

.bar_chart a img{
	display: block;
}

.ruler{
	overflow: visible;
	height: 30px;
	margin: 0 1px 0 0;
	padding: 0;
	list-style: none;
}

.ruler li{
	display: block;
	float: left;
	width: 0;
	overflow: visible;
	text-align: center;
}

.ruler li span{
	height: 5px;
	width: 1px;
	background-color: #ccc;
	display: inline-block;
	vertical-align: top;
}

.ruler li div{
	display: block;
	width: 3em;
	margin-top: -0.7em;
	margin-left: -1.5em;
}

.ruler li:first-child{
	margin-left: 0;
}

.ruler li:nth-child(2){
	margin-left: 45.45%;
}

.ruler li:nth-child(3){
	margin-left: 30.3%;
}

.ruler li:nth-child(4){
	margin-left: 24.25%;
}

.bar_legend{
	overflow: visible;
	margin: 0 1px 0 0;
	padding: 0;
	list-style: none;
	font-size: 0;
  text-align: left;
  height: 100px;
  position: relative;
}

.bar_legend li{
  position: absolute;
	font-size: 1rem;
	text-align: center;
	width: 0;
	display: inline-block;
  bottom: 12px;
}

.bar_legend li span{
	width: 1px;
	background-color: #D3B17D;
	display: inline-block;
	vertical-align: bottom;
}

.bar_legend li:first-child span{
	height: 25px;
}

.bar_legend li:nth-child(2) span{
	height: 45px;
}

.bar_legend li div{
	display: block;
	width: 10em;
	margin-left: -5em;
	margin-bottom: 3px;
}

.sidebar{
	padding-top: 23px;
}

.side_nav{
  top: 23px;
}

.affix-top,
.affix{
	position: relative;
}

.tip{
	margin-top: 10px;
}

.btn_addon_container{
	display: table;
}
.btn_addon_container > *{
	display: table-cell;
}

.btn_addon{
	display: table-cell;
	width: 1%;
}

.btn_addon *:first-child{
	margin-left: -1px;
}

.navi_list{
	padding: 0;
	margin: 0;
	list-style: none;
}

.navi_list li{
  padding: 5px 0;
	display: block;
}

.navi_list li a{
	display: block;
	padding: 0 7px;
	color: #909096;
}

.navi_list li.active a,
.navi_list li:hover a{
	padding-left: 5px;
	border-left: 2px solid #d93344;
	background: transparent;
	color: #f8f8ff;
	text-decoration: none;
	outline: 0 none;
}

.navi_list li a:active,
.navi_list li a:focus{
	background: transparent;
	text-decoration: none;
	outline: 0 none;
}

.glyphicon.glyphicon-check{
	color: #05a658;
}

.instruction{
	margin-top: 23px;
}

.footer{
	padding-top: 30px;
	padding-bottom: 20px;
	margin-top: 40px;
	border-top: 1px solid rgba(0, 0, 0, 0.1);
	min-height: 30vh;
}

@media screen and (min-width: 768px){
	.affix{
		position: fixed;
		top: 23px;
	}
}
